.movie-details {
  background-color: white;
  padding: 20px;
  margin-top: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.movie-details img {
  width: 100%;
  max-width: 400px;
  height: auto;
  margin-bottom: 20px;
}

.movie-details h2 {
  margin-bottom: 10px;
}
